*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: $mobile-width;
  font-family: "Open Sans", "Arial", sans-serif;
  box-sizing: border-box;
  margin: 0;
}


.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
